import {
	classNames,
	widgetSettings
} from './../../lib/utils';
import {
	registerUpdate,
	unregisterUpdate,
	nowCheck
} from './../../lib/r3e';
import { action, observable } from 'mobx';
import { IWidgetSetting } from '../app/app';
import { observer } from 'mobx-react';
import React from 'react';
import style from './clock.scss';

interface IProps extends React.HTMLAttributes<HTMLDivElement> {
	settings: IWidgetSetting;
}

@observer
export default class Clock extends React.Component<IProps, {}> {

	@observable
	localTime = '';

	@observable
	lastCheck = 0;

	constructor(props: IProps) {
		super(props);

		registerUpdate(this.update);
	}

	componentWillUnmount() {
		unregisterUpdate(this.update);
	}

	@action
	private update = () => {
		if (
			nowCheck - this.lastCheck >= 100
		) {
			this.lastCheck = nowCheck;
			const theTime = new Date();
			const hours = theTime.getHours();
			const minutes = theTime.getMinutes();
			const seconds = theTime.getSeconds();
			this.localTime =
				`${
					hours
				}:${
					minutes >= 10
						?	''
						:	'0'
				}${
					minutes
				}:${
					seconds >= 10
						?	''
						:	'0'
				}${
					seconds
				}`;
		}
	};

	render() {
		return (
			<div
				{...widgetSettings(this.props)}
				className={classNames(style.theClock, this.props.className, {
					shouldShow: 1 === 1
				})}
			>
				{/* Clock */}
				<div className="theTime">
					{this.localTime}
				</div>
			</div>
		);
	}
}
